@import '../core/variables.scss';
@import '../core/mixins.scss';

.preview {
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;

  @media screen and (min-width: 764px) {
    width: 80vw;
  }

  @media screen and (min-width: 1024px) {
    width: 60vw;
  }

  &__image {
    object-fit: cover;
    width: 100%;
    height: 80%;
    box-shadow: 0 1px 1px rgba(black, .15),
      0 3px 10px rgba(black, .15),
      0 3px 20px rgba(black, .35);
  }

  &__autor {
    gap: 10px;
    border: none;
    background-color: $white;
    color: $dark-grey;
    width: 90%;
    transform: translateY(-100px);
    margin: auto;
    border-radius: 20px;
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
    gap: 40px;
    padding: 40px;

    @media screen and(min-width:764px) {
      flex-direction: row-reverse;
    }

    &--infoproject {
      width: 100%;
    }

    &--infoautor {
      width: 100%;
      text-align: center;

      @media screen and(min-width:764px) {
        width: 40%;
      }
    }
  }
}

.project {
  &__subtitle {
    font-size: 14px;
    text-align: center;
  }

  &__line {
    width: 100%;
    border: 0;
    height: 1px;
    background-image: linear-gradient(to right,
        rgba(255, 255, 255, 0),
        rgba(1, 1, 1, 0.75),
        rgba(255, 255, 255, 0));
  }

  &__title {
    font-size: 20px;
    color: $black;
    margin-bottom: 10px;
  }

  &__slogan {
    font-size: 14px;
    margin-bottom: 10px;
  }

  &__desc {
    font-size: 12px;
    margin-bottom: 10px;
  }

  &__technologies {
    background-color: $light-grey;
    border-radius: 20px;
    font-size: 14px;
    padding: 10px;
    //width: 70%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 15px;

    &--icon {
      font-size: 20px;
      display: flex;
      gap: 10px;

    }

  }
}

.infoAutor {
  text-align: center;

  &__image {
    border-radius: 50%;
    width: 80px;
    height: 110px;
  }

  &__job {
    font-weight: bold;
    color: $black;
    font-size: 14px;
  }

  &__name {
    font-size: 16px;
  }
}

.reset {
  text-align: center;
}

.resetBtn {
  line-height: 43px;
  font-size: 13px;
  color: $white;
  text-align: center;
  border: none;
  height: 43px;
  width: 120px;
  border-radius: 20px;
  transform: translateY(-50px);
  cursor: pointer;
  background: linear-gradient(90deg, $dark-lilac -1.57%, $light-lilac 98.43%);
}